import React, { ReactNode } from "react";

interface ScrollAreaProps {
  children: ReactNode;
  className?: string;
  maxHeight?: string;
}

export function ScrollArea({ 
  children, 
  className = "",
  maxHeight = "100%" 
}: ScrollAreaProps) {
  return (
    <div 
      className={`overflow-y-auto ${className}`}
      style={{ maxHeight }}
    >
      {children}
    </div>
  );
}
